function CardImage(p) {
  let data = p.propsValue;
  let { device, path, duration } = data;

    return (
      <div className="card_image">
        <span className="background"></span>
        <div className="mockup">
          <div
            className={device[0]}
            data-aos="slide-up"
            data-aos-duration={duration[0]}
          >
            <img src={"./assets/images/" + path + "_desktop.png"} alt="" />
          </div>
          <div
            className={device[1]}
            data-aos="slide-up"
            data-aos-duration={duration[1]}
          >
            <img src={"./assets/images/" + path + "_mobile.png"} alt="" />
          </div>
        </div>
      </div>
    );
}

export default CardImage;