function SkillSwiperLi(p) {
	let data = p.propsValue;
	return (
		<div className="skill">
			<p className="num">{data.num}</p>
			<p className="category">{data.category}</p>
			<ul className="stack">
				{
					data.path.map((d, i) =>
						<li key={i + 1}><img src={process.env.PUBLIC_URL + "/assets/images/" + d + ".svg"} alt={d} /></li>
					)}
			</ul>
			<ul className="skill_desc">
				{
					data.desc.map((d, i) =>
						<li key={i + 1}>{d}</li>
					)}
			</ul>
		</div>
	);
}

export default SkillSwiperLi;