import { useSelector } from "react-redux";
import CasestudyLi from "./CasestudyLi";

function Casestudy(){
    let data=useSelector(state => state.casestudy)

    return(
    <section id="casestudy">
        <h4>Casestudy</h4>
        <div className="sec-inner">
            <ul>
                {
                    data.map((d, i) => <CasestudyLi key={i+1} propsValue={d}/>)
                }
            </ul>
        </div>
    </section>
    )
}

export default Casestudy;