import { useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import gsap from "gsap";
import ScrollToPlugin from "gsap/ScrollToPlugin";
import AOS from "aos";
import "aos/dist/aos.css";

import Nav from "./Nav";

function SubMain() {
	let navigate = useNavigate();
	let location = useLocation();
	let link = location.state;

	useEffect(() => {
		gsap.registerPlugin(ScrollToPlugin);

		if (document.body.classList.contains("dark")) {
			document.body.classList.remove("dark");
		}

		function portfolioListInit() {
			gsap.to(window, { scrollTo: 0, duration: 0 });
		}

		portfolioListInit();

		let gnbLi = document.querySelectorAll("#gnb > ul > li");

		let scrollT;

		let section = [document.getElementById("project"), document.getElementById("contact")];

		let t = 0;
		let n = 3;
		let targety = 0;
		let contactFlag = false;

		let mobileTab = document.querySelector("header.top a.tab");
		let mobileMenu = document.querySelector("nav#mobile");
		let mobileList = mobileMenu.firstElementChild.children;

		let casestudyList = document.querySelectorAll("#casestudy ul li");

		let contactText = document.querySelector("#contact .text_wrap");
		let contactTextWrapper = document.querySelector("#contact .contact-top");

		mobileTab.addEventListener("click", function (e) {
			e.preventDefault();

			if (!e.currentTarget.classList.contains("open")) {
				e.currentTarget.classList.add("open");
				document.body.classList.add("stop-scrolling");

				gsap.fromTo(mobileMenu, { display: "block", opacity: 0 }, { opacity: 1, duration: 0.3 });
			}
			else {
				e.currentTarget.classList.remove("open");
				document.body.classList.remove("stop-scrolling");

				gsap.to(mobileMenu, {
					opacity: 0, duration: 0.3, oncComplete: function () {
						mobileMenu.style.display = "none";
					}
				});
			}
		});

		for (let i = 0; i < gnbLi.length; i++) {
			gnbLi[i].addEventListener("click", function (e) {
				e.preventDefault();
				if (i <= 2) { // 0, 1, 2
					navigate("/", { state: i.toString() });
				}
				else { // 3, 4
					targety = section[i - 3].offsetTop;
					gsap.to(window, { scrollTo: targety, duration: 0.5 });
				}
			})
		}

		for (let i = 0; i < mobileList.length; i++) {
			mobileList[i].addEventListener("click", function (e) {
				e.preventDefault();
				if (i <= 2) { // 0, 1, 2
					navigate("/", { state: i.toString() });
				}
				else { // 3, 4
					targety = section[i - 3].offsetTop;
					if (mobileTab.classList.contains("open")) {
						mobileTab.classList.remove("open");
						document.body.classList.remove("stop-scrolling");
						mobileMenu.style.display = "none";
						gsap.to(window, { scrollTo: targety, duration: 0.7 });
					}
					// gsap.to(window, {scrollTo: targety, duration:0.5});
				}
			})
		}

		let projectList = document.querySelectorAll("#project ul li");
		let projectListBg = document.querySelectorAll("#project ul li a span.background");
		let imageUrl;

		let BgArray = ["MINI", "CP", "nexon", "hybe", "portfolio"]

		for (let i = 0; i < projectList.length; i++) {
			imageUrl = `./assets/images/${BgArray[i]}_desktop.png`;
			projectListBg[i].style.backgroundImage = `url(${imageUrl})`;
		}
		//TextApplication
		function contactTextApplication() {
			if (contactFlag) return;

			contactFlag = true;

			contactText.classList.add("active");

			setTimeout(function () {
				contactText.classList.remove("active");
			}, 1000);
		}
		//Trigger
		function scrollTrigger() {
			let t = window.scrollY;
			let winh = window.innerHeight;

			if (t < section[1].offsetTop) {
				n = 3;

				if (window.innerHeight + t === document.body.scrollHeight) {
					n = 4;
				}
			}
			else {
				n = 4;
			}

			if (n === 4) {
				if (document.body.classList.contains("dark") == false) {
					document.body.classList.add("dark");
					contactTextApplication();
				}
			}
			else {
				if (document.body.classList.contains("dark") == true) {
					document.body.classList.remove("dark");
				}
			}

			for (let i = 0; i < gnbLi.length; i++) {
				if (i === n) {
					if (!gnbLi[i].classList.contains("active")) {
						gnbLi[i].classList.add("active");
						mobileList[i].classList.add("active");
					}
				}
				else {
					if (gnbLi[i].classList.contains("active")) {
						gnbLi[i].classList.remove("active");
						mobileList[i].classList.remove("active");
					}
				}
			}
		};

		scrollTrigger();
		window.addEventListener("scroll", scrollTrigger);

		function resizeTrigger() {
			let winw = window.innerWidth;

			if (winw >= 720) {
				if (document.body.classList.contains("stop-scrolling")) {
					mobileTab.classList.remove("open");
					document.body.classList.remove("stop-scrolling");
					mobileMenu.style.display = "none";
				}
			}
		};

		window.addEventListener("resize", resizeTrigger);

		for (let i = 0; i < casestudyList.length; i++) {

			casestudyList[i].addEventListener("click", function (e) {

				if (!casestudyList[i].classList.contains("active")) {

					for (let j = 0; j < casestudyList.length; j++) {
						if (j === i) {
							casestudyList[j].classList.add("active");
							gsap.fromTo(casestudyList[j].lastElementChild, { display: "block", height: 0 }, { height: "auto", duration: 0.3 });
						}
						else {
							casestudyList[j].classList.remove("active");
							gsap.to(casestudyList[j].lastElementChild, {
								height: 0, display: "none", onComplete: function () {
									casestudyList[j].lastElementChild.removeAttribute("style");
								}
							});
						}
					}
				}
				else {
					if (e.target.tagName === "A" || e.target.tagName === "SPAN") return;
					else {
						casestudyList[i].classList.remove("active");
						gsap.to(casestudyList[i].lastElementChild, {
							height: 0, display: "none", onComplete: function () {
								casestudyList[i].lastElementChild.removeAttribute("style");
							}
						});
					}
				}
			});
		}
		AOS.init({
			easing: "Linear",
			once: true
		});

		return (() => {
			window.removeEventListener("scroll", scrollTrigger);
			window.removeEventListener("resize", resizeTrigger);
		});
	});
	return (
		<>
			<header className="top">
				<h1>CHAHEE<br />PORTFOLIO</h1>
				<Nav />
				<a href="" className="tab">
					<span></span>
					<span></span>
					<span></span>
				</a>
			</header>
		</>
	)
}

export default SubMain;