import Title from "./Title";
import Card from "./Card";
import { useSelector } from "react-redux";

function Project(){
    let data=useSelector(state => state.title);
    return(
        <section id="project">
            <Title propsValue={data[2]}/>
            <div className="sec-inner">
                <ul>
                    <Card />
                </ul>
            </div>
    </section>
    )
}

export default Project;