import { useSelector } from "react-redux";

import CardImage from "./CardImage";
import CardContent from "./CardContent";

function Card(){
  let data=useSelector(state => state.cardcontent);

  return(
    <>
    {
      data.map((d, i)=>
        <li key={i+1}>
          <a href={d.link} target="pop">
            <div className="card">
              <CardImage propsValue={d}/>
              <CardContent propsValue={d}/>
            </div>
          </a>
        </li>
      )
    }    
    </>
  )
};

export default Card;