import TextWrap from "./TextWrap";
import Marquee from "./Marquee";
import { useSelector } from "react-redux";

function KeytextTop() {
  let data = useSelector(state => state.textwrap);

  return (
    <div className="keytext_top">
      <div className="wrap">
        <div className="first">
          <TextWrap propsValue={data[0]} />
          <TextWrap propsValue={data[1]} />
        </div>
        <div className="second"><span>with skills</span></div>
      </div>
      <Marquee />
    </div>
  );
};

export default KeytextTop;