import Title from "./Title";
import SkillSwiper from "./SkillSwiper";

import { useSelector } from "react-redux";

function Skill(){
    let data=useSelector(state => state.title);

    return(
    <section id="skills">
        <Title propsValue={data[1]}/>
        <SkillSwiper />
    </section>
    );
}

export default Skill;