import { useSelector } from "react-redux";
function Button(){
let data=useSelector(state => state.button);
    return(
        <>
        {
            data.map((d, i) => 
                <li key={i+1}>
                    <a href={d.link} target="pop" className="button">
                        <span className="link">
                            <span className="link_inner">
                                <span>{d.desc}</span>
                                <span className="link_animated">{d.desc}</span>
                            </span>
                        </span>
                    </a>
                </li>
            )
        }
        </>
    );
}

export default Button;