import { useSelector } from "react-redux";
function ContactTopInformLi(){
    let data=useSelector(state => state.inform);
    
    return(
        <>
        {
            data.map((d, i) => 
                <li key={i+1}><span>{d.category}</span> <p>{d.desc}</p></li>
            )
        }
        </>
    );
}

export default ContactTopInformLi;