import { useSelector } from "react-redux";
function Nav() {
  let data = useSelector(state => state.navigation);
  return (
    <>
      {
        data.map((d, i) =>
          <nav key={i + 1} id={d.device}>
            <ul>
              {
                d.navigation.map((d, i) => <li key={i + 1}><a href="">{d}</a></li>)
              }
            </ul>
          </nav>
        )
      }
    </>
  );
};

export default Nav;
