import { useSelector } from "react-redux";

function KeytextBottom(){
    let data=useSelector(state => state.keytextbottom);
    return(
        <div className="keytext_bottom">
            {
                data.map((d, i)=> <span key={i+1}>{d}</span>)
            }
    </div>
    );
};

export default KeytextBottom;