import Title from "./Title";
import AboutDesc from "./AboutDesc";

import { useSelector } from "react-redux";

function About(){
    let data=useSelector(state => state.title);

    return(
    <section id="about">
        <Title propsValue={data[0]}/>
        <div className="sec-inner" >
        <AboutDesc />
        </div>
    </section>
    );
}

export default About;