import { useSelector } from "react-redux";

function MarqueeItem() {
  let data = useSelector(state => state.marqueeitem);
  return (
    <>
      {
        data.map((d, i) =>
          <div key={i + 1} className="marquee_item-snap">
            <h2 className="marquee_text">{d}</h2>
          </div>)
      }
    </>
  );
};

export default MarqueeItem;