import TextWrap from "./TextWrap";
import ContactTopInform from "./ContactTopInform";

import { useSelector } from "react-redux";

function ContactTop(){
    let data=useSelector(state => state.textwrap);
    return(
    <div className="contact-top">
        <div className="contact-heading">
            <TextWrap propsValue={data[3]}/>
        </div>
            <ContactTopInform />
    </div>
    );
}

export default ContactTop;