function CasestudyLi(p){
  let data=p.propsValue;
  let {brand, link, desc}=data;
  return(
    <li>
      <strong className="brand">{brand}</strong>
      <div className="hide_desc">
          <p>{desc}</p>
          <a href={link} target="pop"><span>WEBSITE</span></a>
      </div>
    </li>
  )
};

export default CasestudyLi;