import { useSelector } from "react-redux";
function AboutDescSub(p){
    let data=useSelector(state => state.aboutdescsub);

    return(
    <div className="about-sub-text">
        {
            data.map((d, i) =>
                <p data-aos="fade" key={i+1} dangerouslySetInnerHTML={{__html: d}} />
            )
        }
    </div>
    );
}

export default AboutDescSub;