import { useSelector } from "react-redux";
import { Swiper, SwiperSlide } from "swiper/react";
import { Scrollbar } from "swiper/modules";

import 'swiper/css';
import 'swiper/css/scrollbar';
import SkillSwiperLi from "./SkillSwiperLi";

function SkillSwiper(){
	let data=useSelector(state => state.skillswiper);

	return(
	<Swiper
		modules={[Scrollbar]}
		scrollbar={{
		hide: false,
		draggable: true,
		dragSize: 100
		}}
		slidesPerView={1}
		spaceBetween={20}
		initialSlide={0}
		breakpoints={{
		499: {
			slidesPerView: 1.4,
			spaceBetween: 20,
			scrollbar: {
			dragSize: 150
			}
		},
		720: {
			slidesPerView: 2,
			spaceBetween: 20,
			scrollbar: {
			dragSize: 200
			}
		},
		940: {
			slidesPerView: 2.5,
			spaceBetween: 20,
			scrollbar: {
			dragSize: 220
			}
		},
		1280: {
			slidesPerView: 3,
			spaceBetween: 25,
			scrollbar: {
			dragSize: 285
			}
		}}
	}

	className="skill-swiper"
	>
		{
		data.map((d, i) => 
			<SwiperSlide key={i+1}><SkillSwiperLi propsValue={d} /></SwiperSlide>
		)}
	</Swiper>
	);
}

export default SkillSwiper;