function TextWrap(props) {
	let data = props.propsValue;

	return (
		<div className="text_wrap">
			<div className="inline">
				<div className="text_title">
					<span className="slide-up" data-content={data}>
						{data}
					</span>
					{data}
				</div>
			</div>
		</div>
	);
}

export default TextWrap;