import React, { useEffect } from 'react';
import SubMain from "./SubMain";
import Project from "./Project";
import Casestudy from "./Casestudy";
import SubContact from "./SubContact";

function Sub() {
	useEffect(() => {
		window.scrollTo(0, 0);
	})
	return (
		<div className="container">
			<SubMain />
			<div className="works_list">
				<Project />
				<Casestudy />
			</div>
			<SubContact />
		</div>
	)
}

export default Sub;